import React from "react";
import { Link } from 'react-router-dom';
import { Form, Field, FieldArray } from "formik";

import { FaLongArrowAltLeft } from 'react-icons/fa';
import { TiUserAdd, TiUser, TiThList } from 'react-icons/ti';
import { GoCheck } from 'react-icons/go';
import { MdAdd, MdDelete } from 'react-icons/md';

import CustomSelectField from "./order.select-field.component";

import ErrorMessage from "./validation.component"


const OrderForm = ({ isSubmitting, handleChange, values, errors, touched }) => {

	const showTotalPrice = values.order_items.reduce(
		(showTotalPrice, friend) => showTotalPrice + (friend.quantity * friend.price),
		0
	)

	const customerOptions = values.customerdata.map((phantu, i) => ({
		label: phantu.customername, value: phantu.customername, subLabel: phantu.customer_info[0].address
	})
	)


	const productOptions = values.productdata.map((val, index) => ({
		label: val.product, value: val.product, subLabel: val.model
	})
	)


	return (
		<Form autoComplete="off">
			<div className="row">
				<div className="col-12 p-© border-bottom mb-4 bg-light">
					<small className="text-monospace text-muted text-uppercase ml-2">Ngày tạo: {values.date}</small>
				</div>

				<div className="col-12">
					<div className="row">
						{/********** Customer Name ***********/}

						<div className="form-group col-12">
							<div className="input-group input-group-lg w-100">
								<div className="input-group-prepend">
									<label className="input-group-text border-0 bg-transparent"><TiUser className="text-secondary" /></label>
								</div>
								<Field
									className="form-control form-control-lg custom-select border-0 p-0"
									name="customername"
									options={customerOptions}
									component={CustomSelectField}
									isMulti={false}
									placeholder={<div className="text-muted">Nhập khách hàng</div>}
								/>
								<ErrorMessage className="ml-5" name="customername" />
								<div className="input-group-append">
									<Link to="/customer" className="btn  btn-lg bg-transparent border-right border-top border-bottom border-left-none"><TiUserAdd /></Link>
								</div>
							</div>
						</div>

						{/********** Product ***********/}
						<div className="col-12  mb-4">
							<div className="row">
								<FieldArray
									name="order_items"
									render={arrayHelpers => (
										<>
											{values.order_items &&
												values.order_items.map(
													(friend, index) => (

														<div className="form-row mb-3 col-12" key={index} >
															<div className="input-group input-group-lg col-4">
																<div className="input-group-prepend">
																	<span className="text-secondary input-group-text border-0 bg-transparent">
																		<TiThList />
																	</span>
																</div>
																<Field
																	required
																	className="form-control form-control-lg  border-0 p-0"
																	name={`order_items[${index}].product`}
																	options={productOptions}
																	component={CustomSelectField}
																	placeholder={<div className="text-muted">Nhập sản phẩm</div>}
																	isMulti={false}
																/>
																<ErrorMessage name={`order_items[${index}].product`} />
															</div>
															<div className="form-group col-8">
																<div className="input-group input-group-lg" >

																	<Field
																		required
																		type="number"
																		className="form-control"
																		placeholder="Số lượng"
																		name={`order_items[${index}].quantity`}
																	/>
																	<ErrorMessage name={`order_items[${index}].quantity`} />


																	<Field
																		type="number"
																		className="form-control"
																		placeholder="0.000đ"
																		name={`order_items[${index}].price`}

																	/>

																	<div className="input-group-append">
																		<button
																			type="button"
																			className="btn  btn-lg bg-transparent border-right border-top border-bottom border-left-none"
																			onClick={() =>
																				arrayHelpers.remove(
																					index
																				)
																			}
																		>
																			<MdDelete />
																		</button>
																	</div>

																</div>
															</div>
														</div>

													)
												)}
											<div className="col-12">
												<div className="row">
													<div className="col-4">
														<button
															type="button"
															className="btn btn-light btn-link  w-100"
															onClick={() =>
																arrayHelpers.push({
																	product: "",
																	quantity: "",
																	price: ""
																})
															}
														>
															<MdAdd /> Thêm sản phẩm
												</button>
													</div>

													<div className="col-8 d-flex align-items-center justify-content-between">
														<div className="form-group form-check mb-0">
															<Field
																component="input"
																className="form-check-input"
																type="checkbox"
																name="paid_status"
																id="paid_status"
																checked={values.paid_status}
																onChange={handleChange}
															/>
															<label className="form-check-label" htmlFor="paid_status" >Đã Thanh Toán</label>
														</div>
														<div className="form-group mb-0">
															<label className="text-right mb-0">Tổng cộng: </label>
															<span className="text-monospace font-weight-bold">
																{showTotalPrice}.000đ
													</span>
														</div>

													</div>
												</div>
											</div>

										</>
									)}
								/>
							</div>
						</div>

						{/********** Ghi chú ***********/}
						<div className="col-12">
							<div className="form-group">
								<label>Ghi chú</label>
								<Field type="text" className="form-control" name="description" placeholder=" " />
								<ErrorMessage name="description" />
								{/* {errors.description && touched.description ? <div className="alert alert-warning">{errors.description}</div> : null} */}
							</div>
						</div>
					</div>
				</div>

				{/********** Submit ***********/}
				<div className="form-group d-flex justify-content-between align-items-center col-12 bg-light mb-0 p-4 border-top">
					<a href="/" className="btn btn-link mr-5 btn-sm"><FaLongArrowAltLeft /> Bỏ Qua</a>
					<button
						type="submit"
						disabled={isSubmitting}
						className="btn btn-primary btn-lg"><GoCheck className="mr-2" />Tạo Hoá Đơn</button>
				</div>
			</div>
		</Form>

	);
};


export default OrderForm;
