import React, { Component, Fragment } from "react";
import { Link } from "react-router-dom";
import axios from "axios";

import { AiFillFileAdd } from "react-icons/ai";
import { TiUser } from 'react-icons/ti';
import { MdEdit, MdAttachMoney, MdDelete, MdPrint, MdMoreVert, MdMoneyOff } from "react-icons/md";
import { FiPackage } from 'react-icons/fi';


import Swal from "sweetalert2";

function Order(props) {
	// Dialog
	function deleteOrderModal() {
		Swal.fire({
			title: 'Xoá đơn hàng này?',
			text: props.order.customername,
			icon: 'warning',
			showCancelButton: true,
			confirmButtonColor: '#3085d6',
			cancelButtonColor: '#d33',
			confirmButtonText: 'Xoá!'
		}).then((result) => {
			if (result.value) {
				Swal.getConfirmButton(props.deleteOrder(props.order._id))
				Swal.fire(
					'Đã xoá đơn!',
					'',
					'success',
				)
			} else {
				window.location = '/'
			}
		})
	}


	function updatePaidStatus() {
		Swal.fire({
			title: 'Cập nhật tình trạng thanh toán',
			text: props.order.customername,
			icon: 'warning',
			showCancelButton: true,
			confirmButtonColor: '#3085d6',
			cancelButtonColor: '#d33',
			confirmButtonText: 'Xoá!'
		}).then((result) => {
			if (result.value) {
				Swal.getConfirmButton(props.deleteOrder(props.order._id))
				Swal.fire(
					'Đã xoá đơn!',
					'',
					'success',
				)
			} else {
				window.location = '/'
			}
		})
	}
	// End of Dialog

	function PaidStatus() {
		let status = props.order.paid_status
		if (status === true) {
			return <span className="badge  badge-success mr-3 " data-toggle="tooltip" data-placement="top" title="Tooltip on top"><MdAttachMoney /></span>
		} else {
			return <span className="badge  badge-warning mr-3" data-toggle="tooltip" data-placement="top" title="Tooltip on top"><MdMoneyOff /></span>
		}
	}

	function Description() {
		if (props.order.description !== "") {
			return <p className="mb-0 text-center mb-0"><em>{props.order.description}</em></p>
		} else {
			return <p className="text-muted text-center mb-0">...</p>
		}
	}


	const currencyFormat = new Intl.NumberFormat('en-US', {
		style: 'decimal',
		minimumFractionDigits: 3
	})

	function ShowTotalPrice() {
		let i =
			props.order.order_items.reduce(
				(showTotalPrice, order) => showTotalPrice + (order.quantity * order.price), 0
			)
		return currencyFormat.format(i)
	}

	console.log("type of total:" + typeof ShowTotalPrice());

	return (
		<div className="list-group-item list-group-item-action d-flex justify-content-between align-items-center p-0">
			<div className="col-2 " key={props.order.customername}>
				<div className="d-flex w-100  align-items-center">
					<small className="text-muted mr-4">{props.index + 1}</small>
					<TiUser className="mr-2" /><h6 className="mb-0">{props.order.customername}</h6>
				</div>
			</div>
			<div className="col-4">

				<ul className="list-group my-2">
					{props.order.order_items.map(({ product, quantity, price }) => (
						<Fragment key={product}>
							<li className="list-group-item">
								<div className="row">
									<div className="col-5">
										<span>
											{product}
										</span>
									</div>
									<div className="col-3">
										<span className="d-flex align-items-center">
											<FiPackage className="mr-2" />{quantity}
										</span>
									</div>
									<div className="col-4 text-right">
										<span className="d-flex align-items-center">
											<MdAttachMoney />{currencyFormat.format(price)}
										</span>
									</div>
								</div>
							</li>
						</Fragment>
					))}
				</ul>

			</div>
			<div className="col-2 d-flex align-items-stretch p-1">
				<a href="/#"
					className="btn btn-light d-flex  align-items-center w-100"
					onClick={() => {
						updatePaidStatus();
					}}
				>
					<PaidStatus status={true} />
					<ShowTotalPrice />

				</a>


			</div>
			<div className="col-1">
				<small className="text-muted">{props.order.date.substring(0, 10)}</small>
			</div>

			<div className="col-2" key={props.order.description}>
				<Description />
			</div>

			<div className="controller text-right col-1">
				<div className="btn-group btn-group-lg" role="group" aria-label="Button group with nested dropdown">
					<button type="submit" className="btn btn-link btn-lg text-muted"><MdPrint /></button>
					<div className="dropdown" role="group">
						<Link
							className="btn btn-link btn-lg  text-muted"
							id={props.order._id}
							to="/#"
							role="button"
							data-toggle="dropdown"
							aria-haspopup="true"
							aria-expanded="false"
						><MdMoreVert />
						</Link>

						<div className="dropdown-menu" aria-labelledby={props.order._id}>
							<Link
								to={"/edit/" + props.order._id}
								className="dropdown-item"
							>
								<MdEdit className="mr-2" />Sửa
							</Link>
							<Link to="/#"
								onClick={() => {
									deleteOrderModal();
								}}
								alt={props.order._id}
								className="dropdown-item"
							>
								<MdDelete className="mr-2" />Xoá
							</Link>
						</div>
					</div>
				</div>
			</div>
		</div >
	);
}


export default class OrdersList extends Component {
	constructor(props) {
		super(props);
		this.deleteOrder = this.deleteOrder.bind(this);
		this.state = {
			orders: []
		};
	}

	componentDidMount() {
		axios
			.get("http://localhost:5000/orders/")
			.then(response => {
				this.setState({ orders: response.data });
			})
			.catch(error => {
				console.log(error);
			});
	}

	deleteOrder(id) {
		axios
			.delete("http://localhost:5000/orders/" + id)
			.then(res => console.log("Một hoá đơn vừa được xoá: ", res.data));
		this.setState({
			orders: this.state.orders.filter(el => el._id !== id)
		});
	}

	OrderList() {
		let orders = this.state.orders;
		if (orders === null || orders.length === 0) {
			console.log('Chưa có dữ liệu')
			return <div className="alert alert-info text-center col-6 mx-auto" role="alert">
				Chưa có đơn hàng nào được tạo!
		  </div>
		} else {
			return [...this.state.orders].reverse().map((currentorder, i) => {
				return <Order
					index={i}
					order={currentorder}
					deleteOrder={this.deleteOrder}
					key={currentorder._id}
				/>

			});
		}
	}



	render() {
		return (
			<div className="col-12">
				<div className="row">
					<div className="col-12 d-lg-flex justify-content-between mb-5">
						<h3>Danh sách hoá đơn</h3>
						<Link
							to="/create"
							className="btn btn-lg btn-primary d-flex align-items-center"
						>
							<span className="mx-auto"><AiFillFileAdd /> <span className="ml-2">Tạo Đơn Hàng Mới</span></span>
						</Link>
					</div>

					<div className="col-12">
						<div className="row">
							<div className="col-12">
								<div className="list-group mb-5">
									{this.OrderList()}
								</div>
							</div>
						</div>
					</div>
				</div>

			</div>
		);
	}
}
